


























































































	export default{
		data:{
			order:[],//订单数据
			checked : false,
			checkedAll : false,
			allNames: [],//购物车内所有商品id，用来对比是否全选中
			checkedNames: [],//已经选中的商品id，==allNames时，全选
			total_price: 0.00,//选中商品总价
			uid:'',
			is_login: false
		},
		components:{
			
		},
		methods:{
			//获取所有购物车内商品信息
			islogin:function(){
				var _this = this;
				wx.checkSession({
				success () {
					//session_key 未过期，并且在本生命周期一直有效
					_this.is_login = true;
				},
				fail () {
					// session_key 已经失效，需要重新执行登录流程
					_this.is_login = false;
				}
			})
			},
			getCartInfo: function(){
				var _this = this;
				this.islogin();
				if(_this.is_login){
					_this.uid = uni.getStorageSync('uid');
					wx.request({
						url: 'https://www.slluks.com/index.php/app/member/cartList',
						method : "POST",
						data: {
							'buyer_id' : _this.uid
						},
						header: {
							'content-type': 'application/json' // 默认值
						},
						success: function(res) {
							_this.order = [];
							_this.allNames = [];
							_this.checkedNames = [];
							_this.total_price = 0.00;
							console.log(_this.uid);
							if(JSON.stringify(res.data.cartList)!='[]'){
								res.data.cartList.forEach(
									item => {
										item.checked = false;
										_this.allNames.push(item.cart_id);
										_this.order.push(item);
									}
								);
								if(res.data.message){
									uni.showToast({
										title: res.data.message,
										duration: 2000,
										icon: 'none'
									});
								}else{
									uni.showToast({
										title: '加载成功',
										duration: 2000,
										icon: 'none'
									});
								}
							}else{
								uni.showToast({
									title: '购物车空空如也',
									duration: 2000,
									icon: 'none'
								});
							}
						},
						fail: function(){
							uni.showToast({
								title: '加载失败',
								duration: 2000,
								icon: 'none'
							});
						}
					})
				}else{
					uni.showToast({
						title: '请先登陆',
						duration: 2000,
						icon: 'none'
					});
				}
			},
			//商品选择状态改变时调用
			checkboxChange:function(e){
				//判断是否全选
				var _this = this;
				if(e.detail.value.length == this.order.length){
					this.checkedAll = true;
				}else if(e.detail.value.length < this.order.length){
					this.checkedAll = false;
				}
				this.checkedNames = e.detail.value;
				this.total_price = 0.00;
				this.order.forEach(
					item => {
						if(_this.inarray(item.cart_id,_this.checkedNames) ){
							_this.total_price += item.price*item.num;
						}
					}
				)
				_this.total_price = _this.total_price.toFixed(2);
				console.log(e.detail.value)
			},
			//全选选项改变时调用
			checkAllChange:function(e){
				var _this = this;
				console.log(e.detail.value[0]);
				if(e.detail.value[0] == "ok"){
					for(var i = 0 ; i < this.order.length ; i++){
						this.order[i].checked = true;
					}
					this.checkedNames = this.allNames;
					this.checkedAll = true;
					this.total_price = 0.00;
					this.order.forEach(
						item => {
							if(_this.inarray(item.cart_id,_this.checkedNames) ){
								_this.total_price += item.price*item.num;
							}
						}
					)
					console.log(this.checkedNames);
				}else if(e.detail.value[0] == undefined){
					this.checkedNames = [];
					this.checkedAll = false;
					this.total_price = 0.00;
					for(var i = 0 ; i < this.order.length ; i++){
						this.order[i].checked = false;
					}
					console.log(this.checkedNames);
				}
			},
			inarray:function(search,array){
				for(var i in array){
					if(array[i]==search){
						return true;
					}
				}
				return false;
			},
			setOrder:function(){
// 				var _this = this;
// 				wx.request({
// 					url: "https://www.slluks.com/index.php/app/member/setOrder",
// 					method: "POST",
// 					data:{
// 						"buyer_id" : _this.uid,
// 						"cart_id" : JSON.stringify(_this.checkedNames)
// 					},
// 					header: {
// 						'content-type': 'application/json' // 默认值
// 					},
// 					success:function(res){
// 						console.log(res.data)
// 					}
// 				})
				if(JSON.stringify(this.checkedNames)!='[]'){
					uni.navigateTo({
						url: "/pages/creatorder/creatorder?uid="+this.uid+"&&cart_id="+JSON.stringify(this.checkedNames)
					})
				}else{
					uni.showToast({
						title: '没有选中商品',
						duration: 2000,
						icon: 'none'
					})
				}
			}
		},
		onLoad() {
			this.getCartInfo();
		},
		onPullDownRefresh() {
			
		},
		onReachBottom() {
			
		},
		onTabItemTap(){
			this.getCartInfo();
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		}
	}
